
import React, { useState } from "react";
import { Box, Typography, Button } from "@mui/material";
import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/css";
import "swiper/css/pagination";
import { Pagination } from "swiper/modules";
import "./LoveLetter.css"; // Ensure this includes the flip effect

function LoveLetter() {
  const [flipped, setFlipped] = useState(false);
  const [page, setPage] = useState(1);

  const loveLetters = [
    {
      title: "Valentines Day ",
      receiver: "To: BB ",
      date: "14 Feb 2025",
      messages: [
        "Meow  Hello , ,  design, ,  :   , postcard ",

  "postcard   ( ), ,     ,   ,  , ,  ,     omakase ,   ,  timeline ,  ha ,    , hea ",
  " , ,  DD,  ,   , ,  , D , , ,  ",

  " ,  ,  ,    ,  ,  , , ,   ,  ,   'Sukidakara'  : ,   ,  ,   , ,  ,   ",

  " boohi  Now, I'm going from underground    , ,   ! ,  33   ,  ,  Enjoy Valentine's Day "
      ],
      image: "https://y1plau.github.io/valentines-website/postcard.jpg",
    },
    {
      title: "Valentines Day ",
      receiver: "To: BB",
      date: "14 Feb 2025",
      messages: [
        "BB:\n\n ,,,,,~ \n\n",
      ],
      image: "https://y1plau.github.io/valentines-website/catpostcard.jpg",
    },
  ];

  return (
    <Swiper pagination={{ clickable: true }} modules={[Pagination]} className="hero-slider">
      {loveLetters.map((letter, index) => (
        <SwiperSlide key={index}>
          <Box
            sx={{
              textAlign: "center",
              height: "100vh",
              display: "flex",
              flexDirection: "column",
              alignItems: "center",
              justifyContent: "center",
            }}
          >
            {/* Title, Receiver, and Date */}
            <Typography variant="h5" sx={{ fontWeight: "bold", color: "white" }}>
              {letter.title}
            </Typography>
            <Typography variant="subtitle1" sx={{ color: "white" }}>
              {letter.receiver}
            </Typography>
            <Typography variant="subtitle2" sx={{ color: "white" }}>
              {letter.date}
            </Typography>

            {/* Flip Card */}
            <Box className={`flip-card ${flipped ? "flipped" : ""}`} onClick={() => setFlipped(!flipped)}>
              <Box className="flip-card-inner">
                {/* Front */}
                <Box
  className="flip-card-front"
  sx={{
    backgroundImage: `url('${letter.image}')`,
    backgroundPosition: "center",
    border: "10px solid white", // Add white border
    borderRadius: "10px", // Optional: rounded corners
    boxShadow: "0 4px 8px rgba(0, 0, 0, 0.2)", // Optional: shadow
  }}
/>

                {/* Back */}
                <Box className="flip-card-back">
                <Typography
  variant="body1"
  sx={{
    padding: "20px",
    color: "black",
    whiteSpace: "pre-line",
    wordWrap: "break-word",
    overflowWrap: "break-word",
    textAlign: "left",
    maxHeight: "100%",  // Prevent text from exceeding card height
    overflowY: "auto",  // Enable scrolling for long content
  }}
>
  {letter.messages[page - 1]}
</Typography>

                
                    {/* Page Navigation Buttons */}
            <Box
              sx={{
                display: 'flex',
                justifyContent: 'flex-end',
                width: '100%',
                position: 'absolute',
                bottom: '20px',
                right: '20px',
              }}
            >
              <Button
                variant="outlined"
                onClick={(e) => {
                  e.stopPropagation(); // Prevent the event from reaching the flip-card
                  setPage(page - 1);
                }}
                disabled={page === 1}
                sx={{ marginRight: '10px' }}
              >
                <i className="fas fa-arrow-left"></i>
              </Button>
              <Button
                variant="outlined"
                onClick={(e) => {
                  e.stopPropagation(); // Prevent the event from reaching the flip-card
                  setPage(page + 1);
                }}
                disabled={page === letter.messages.length}
              >
                <i className="fas fa-arrow-right"></i>
              </Button>
            </Box>
                </Box>
              </Box>
            </Box>
          </Box>
        </SwiperSlide>
      ))}
    </Swiper>
  );
}

export default LoveLetter;
